import token from '../store/token'

const Cookies = require('js-cookie')

export default {
    isLogin () {
        let token = Cookies.get('user_token')
        if (!token) {
            return false
        }
        return true
    },
    logout () {
        Cookies.remove('user_token')
    },
    token () {
        return Cookies.get('user_token')
    },
    setToken(token){
        Cookies.set('user_token', 'asd')
    }
}



