<template>
    <div class="index-page">
        <router-link :to="{name:'home'}">
            <van-nav-bar
                style="z-index: 1001"
                title="明细" fixed="" left-arrow left-text="返回" right-text="X"></van-nav-bar>
        </router-link>
        <div style="height: 50px;"></div>
        <van-cell-group>
            <van-field
                v-model="task.title"
                label="任务标题"
                placeholder="请填写"
                required>
            </van-field>
            <van-field label="数据库驱动" value="mysql"></van-field>
            <van-field
                v-model="task.db_config.db_host"
                label="db_host"
                placeholder="请填写"
                required>
            </van-field>
            <van-field
                v-model="task.db_config.db_port"
                label="db_port"
                placeholder="请填写"
                required>
            </van-field>
            <van-field
                v-model="task.db_config.db_user"
                label="db_user"
                placeholder="请填写"
                required>
            </van-field>
            <van-field
                v-model="task.db_config.db_pwd"
                label="db_pwd"
                placeholder="请填写"
                required>
            </van-field>
            <van-field
                v-model="task.db_config.db_name"
                label="db_name"
                placeholder="请填写"
                required>
            </van-field>
            <van-field
                v-model="task.run_time.Y"
                label="备份(年)"
                placeholder="*">
            </van-field>
            <van-field
                v-model="task.run_time.M"
                label="备份(月)"
                placeholder="*">
            </van-field>
            <van-field
                v-model="task.run_time.D"
                label="备份(日)"
                placeholder="*">
            </van-field>
            <van-field
                v-model="task.run_time.H"
                label="备份(时)"
                placeholder="*">
            </van-field>
            <van-field
                v-model="task.run_time.m"
                label="备份(分)"
                placeholder="*">
            </van-field>
            <van-field
                v-model="task.run_time.s"
                label="备份(秒)">
            </van-field>
            <van-cell title="备份数据表" is-link
                      :value="'已选择'+task.tables.length+'个表'"
                      @click="showChooseTables"></van-cell>

            <van-cell title="备份日志" is-link
                      :value="'已备份'+logs.length+'次'"
                      @click="showLogs"></van-cell>

            <van-switch-cell v-model="task.status" title="开启"/>

        </van-cell-group>
        <van-row>
            <van-col span="24">
                <van-button type="danger" bottom-action @click="remove">删除</van-button>
            </van-col>
        </van-row>
        <van-popup v-model="layer.show" position="top" class="van-popup-1" :overlay="false">
            <span v-html="layer.msg"></span>
        </van-popup>
        <div>
            <!--Table 弹层-->
            <van-popup v-model="layerChooseTable.show" position="right" class="van-popup-4"
                       :overlay="true"
                       style="overflow-y: scroll"
                       :lock-on-scroll="true"
                       :lockOnScroll="true">
                <div class="">
                    <van-row>
                        <van-checkbox-group v-model="task.tables">
                            <van-cell-group>
                                <van-cell v-for="(item, index) in tables" :key="index">
                                    <van-checkbox :name="item">{{ item }}</van-checkbox>
                                </van-cell>
                                <van-cell v-if="tables.length<=0" class="text-center">
                                    <span v-html="layerChooseTable.msg"></span>
                                </van-cell>
                            </van-cell-group>
                        </van-checkbox-group>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-button type="primary" bottom-action @click="sureTables">确定</van-button>
                        </van-col>
                    </van-row>
                </div>
            </van-popup>
        </div>
    </div>
</template>

<script>
    import auth from '@/utils/auth'
    import moment from 'moment'

    export default {
        name: 'hello',
        data () {
            return {
                task: {
                    title: '备份任务' + Math.random(),
                    db_config: {
                        "db_host": "127.0.0.1",
                        "db_port": "3306",
                        "db_user": "root",
                        "db_pwd": "zhaojun",
                        "db_name": "xc_1"
                    },
                    tables: [],
                    run_time: {
                        s: '10',
                        m: '*',
                        H: '*',
                        D: '*',
                        M: '*',
                        Y: '*'
                    },
                    status: true
                },
                tables: [],
                logs: [],
                layer: {
                    show: false,
                    msg: '..'
                },
                layerChooseTable: {
                    show: false,
                    msg: ''
                },
                isLoading: false
            }
        },
        methods: {
            parseTime(time){
                time = time * 1000;
                return moment(time).format('YYYY-MM-DD HH:mm:ss');
            },
            remove(){
                this.$api.deleteTask([this.task._id]);
                this.$router.push({
                    name: 'home'
                })
            },
            save () {
                this.$api.saveTask(this.task).then((res) => {
                    if (res.data.code === 1) {
                        this.layer.show = true;
                        this.layer.msg = res.data.msg;
                        setTimeout(() => {
                            this.$router.push({
                                name: 'home'
                            })
                        }, 1500);
                    }
                })
            },
            showChooseTables(){
                this.tables = [];
                this.$api.getTableDb(this.task).then((data) => {
                    if (data.data.code === 1) {
                        data.data.data.forEach((it) => {
                            this.tables.push(it);
                        })
                    } else {
                        this.layerChooseTable.msg = data.data.msg;
                    }
                }).catch((err) => {

                })
                this.layerChooseTable.show = true;
            },
            showLogs(){
                this.$router.push({
                    name: 'BakLogs',
                    params: {
                        _id: this.task._id
                    }
                })
            },
            sureTables(){
                this.layerChooseTable.show = false;
            },
            getTask(task){
                task.status = task.status === 1
                this.task = task;
            }
        },
        created(){
            if (!this.$route.params._id) {
                this.$router.push({
                    name: 'home'
                })
                return false;
            }
            this.getTask(this.$route.params);
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    .van-popup-1 {
        height: 5em;
        line-height: 5em;
        text-align: center;
        background: #000000;
        color: #fff;
        opacity: .8;
    }

    .van-popup-4 {
        height: 100%;
        width: 100%;
    }

    .text-center {
        text-align: center;
    }
</style>



