import axios from 'axios'
import auth from './auth'
import qs from 'qs'

const instance = axios.create({
    baseURL: 'api',
    //timeout: 1000,
    /*Http Header*/
    // headers: {
    //     'X-Custom-Header': 'foobar',
    //     'X-User-Token': auth.token(),
    //     'X-User-Device': 'Wap',
    //     'X-Browser': 'chrome'
    // }
    // headers: {
    //     'Content-Type': 'application/json'
    // }
})

const API_URLS = {
    portal: {
        login: 'portal/login'
    },
    user: {
        account: 'user.user/account',
        profile: 'user.user/profile',
        //重置密码
        password: 'user.user/password',
        userInfo: 'user.user/userInfo',
    },
    task: {
        tasks: 'task/tasks',
        add: 'task/push',
        del: 'task/del',
        logs: 'task/logs'
    },
    bak: {
        logs: 'bak_logs'
    },
    table: {
        //Method:POST,Body:task
        db: 'table/db'
    }

}

export default {
    login (user) {
        return instance.post(API_URLS.portal.login, qs.stringify(user))
    },
    account () {
        return instance.get(API_URLS.user.account)
    },
    profile () {
        return instance.get(API_URLS.user.profile)
    },
    getTasks () {
        return instance.get(API_URLS.task.tasks)
    },
    addTask(task){
        return instance.post(API_URLS.task.add, {task: task})
    },
    getTableDb(task){
        return instance.post(API_URLS.table.db, {task: task})
    },
    /*获取备份日志*/
    getBakLogs(task, date){
        return instance.get(API_URLS.bak.logs + '/' + date + '?' + qs.stringify({_id: task._id}))
    },
    deleteTask(_ids){
        return instance.delete(API_URLS.task.del + '?' + qs.stringify({ids: _ids}))
    }

}



