<template>
    <div class="protal-login">
        <h3 class="text-center web-title">自动化备份工具@god</h3>
        <van-cell-group style="margin-top: 50px;">
            <van-field
                v-model="username"
                label="用户名"
                icon="clear"
                placeholder="请输入用户名"
                required
                @click-icon="username = ''">
            </van-field>

            <van-field
                v-model="password"
                type="password"
                label="密码"
                placeholder="请输入密码"
                required>
            </van-field>
        </van-cell-group>

        <van-row>
            <van-col span="24">
                <van-button type="primary" bottom-action @click="login">立即登录</van-button>
            </van-col>
        </van-row>
    </div>
</template>

<script>
    import {Toast} from 'vant'
    import auth from '@/utils/auth'

    export default {
        name: 'hello',
        data() {
            return {
                username: '',
                password: ''
            }
        },
        methods: {
            login() {
                let _self = this
                this.$api.login({
                    username: this.username,
                    password: this.password
                }).then(res => {
                    if (res.data.code === 1) {
                        auth.setToken('1')
                        Toast.success(res.data.msg)
                        setTimeout(() => {
                            console.log("red")
                            this.$router.push({
                                name: 'home'
                            })
                        }, 1000)
                    } else {
                        Toast.fail(res.data.msg)
                    }
                })
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

    .text-center {
        text-align: center;
        font-size: 20px;
    }

    .web-title {
        margin-top: 2em;
        font-size: 30px;
    }
</style>



