<template>
    <div class="index-page">
        <van-nav-bar
            style="z-index: 1001"
            title="自动化备份工具" fixed="">
        </van-nav-bar>
        <div style="height: 50px"></div>
        <van-cell-group>
            <van-cell title="暂无数据" style="text-align: center" v-if="tasks.length<=0"></van-cell>
            <van-cell @click="taskDetail(itm)"
                      v-for="itm in tasks"
                      :title="itm.title?itm.title:'未命名'"
                      :value="itm.db_config.db_host" is-link>
            </van-cell>
        </van-cell-group>
        <van-row>
            <van-col span="24">
                <van-button type="primary" bottom-action @click="addTask">添加备份计划</van-button>
            </van-col>
            <van-col span="24" style="margin-top: 10px;">
                <router-link :to="{name:'BakLogs'}">
                    <van-button type="primary" bottom-action @click="addTask">查询备份日志</van-button>
                </router-link>
            </van-col>
        </van-row>
    </div>
</template>

<script>
    import auth from '@/utils/auth'
    import VanCell from '../../../node_modules/vant/packages/cell/index'

    export default {
        components: {VanCell}, name: 'hello',
        data() {
            return {
                tasks: [],
                title: '同意协议'
            }
        },
        methods: {
            addTask() {
                this.$router.push({
                    name: 'TaskAdd'
                })
            },
            getAllTask() {
                this.tasks = [];
                this.$api.getTasks().then((data) => {
                    if (data.data.code === 1) {
                        data.data.data.forEach((it) => {
                            this.tasks.push(it);
                        })
                    }
                }).catch((err) => {

                })
            },
            taskDetail(it) {
                console.log(it);
                this.$router.push({
                    name: 'TaskDetail',
                    params: it
                })
            }
        },
        created() {
            this.getAllTask();
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .style {
        background: #ddd;
    }
</style>



