<template>
    <div class="index-page">
        <van-nav-bar style="z-index: 1001" title="备份日志" fixed="">
        </van-nav-bar>
        <div style="height: 50px"></div>
        <van-cell-group>
            <van-cell title="暂无数据" style="text-align: center" v-if="logs.length<=0"></van-cell>
            <van-cell v-for="item in logs"
                      class="log-item"
                      :title="item.task.title+'：'+item.task.db_config.db_host+'@'+item.task.db_config.db_name"
                      :label="parseTime(item.time)"
                      @click="download(item)" is-link value="下载">
            </van-cell>
        </van-cell-group>
        <van-row>
            <van-col span="24">
                <router-link :to="{name:'home'}">
                    <van-button type="primary" bottom-action>返回</van-button>
                </router-link>
            </van-col>
        </van-row>
    </div>
</template>

<script>
    import auth from '@/utils/auth'
    import VanCell from '../../../node_modules/vant/packages/cell/index'
    import moment from 'moment'

    export default {
        components: {VanCell}, name: 'hello',
        data() {
            return {
                logs: [],
            }
        },
        methods: {
            parseTime(time) {
                time = time * 1000;
                return moment(time).format('YYYY-MM-DD HH:mm:ss');
            },
            getTaskLogs(_id) {
                this.logs = [];
                let date = moment().format('YYYY-MM-DD')
                this.$api.getBakLogs({_id: _id}, date).then((data) => {
                    if (data.data.code === 1) {
                        data.data.data.forEach((it) => {
                            this.logs.push(it);
                        })
                    } else {
                        this.layerLogs.msg = data.data.msg;
                    }
                }).catch((err) => {

                })
            },
            download(item) {
                window.open(item.file);
            }
        },
        created() {
            console.log(this.$route.params._id);
            this.getTaskLogs(this.$route.params._id);
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    .style {
        background: #ddd;
    }
</style>



