import Login from '@/views/portal/Login'
import Register from '@/views/portal/Register'

export default [
    {
        path: '/portal/login',
        name: 'login',
        component: Login,
        meta: {
            requiresAuth: false
        }
    },
    {
        path: '/portal/register',
        name: 'register',
        component: Register,
        meta: {
            requiresAuth: false
        }

    }
]



