import TaskAdd from '@/views/task/TaskAdd'
import TaskDetail from '@/views/task/TaskDetail'

export default [
    {
        path: '/task/addtask',
        name: 'TaskAdd',
        component: TaskAdd,
        meta: {
            requiresAuth: true
        }
    },
    {
        path: '/task/detail',
        name: 'TaskDetail',
        component: TaskDetail,
        meta: {
            requiresAuth: true
        }
    },
]



