<template>
  <div class="accept-page">
    <h1>同意协议</h1>
    <van-row>
      <van-col span="12">
        <van-button bottom-action @click="logout">拒绝协议</van-button>
      </van-col>
      <van-col span="12">
        <van-button type="primary" bottom-action @click="accept">同意协议</van-button>
      </van-col>
    </van-row>
  </div>
</template>

<script>
  import auth from '@/utils/auth'

  export default {
    name: 'hello',
    data () {
      return {
        title: '同意协议'
      }
    },
    methods: {
      logout () {
        auth.logout()
        this.$router.push({
          name: 'login'
        })
      },
      accept () {
        this.$router.push({
          name: 'home'
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



