import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

import Accept from '@/views/pages/Accept'
import Index from '@/views/index/Index'

import auth from '@/utils/auth'
/*portal*/
import portal from './portal'
import task from './task'
import bak from './bak'

Vue.use(Router)

const rooter = [
    {
        path: '/',
        name: 'index',
        component: Index,
        meta: {
            requiresAuth: true
        }
    },
    {
        path: '/index/index',
        name: 'home',
        component: Index,
        meta: {
            requiresAuth: true
        }
    },
    {
        path: '/index/accept',
        name: 'accept',
        component: Accept,
        meta: {
            requiresAuth: true
        }
    },

]
const router = new Router({
    routes: rooter.concat(portal).concat(task).concat(bak)
})

/*路由拦截*/

router.beforeEach((to, from, next) => {
    /*查看是否需要权限*/
    if (to.matched.some(record => record.meta.requiresAuth)) {
        // this route requires auth, check if logged in
        // if not, redirect to login page.

        if (!auth.isLogin()) {
            next({
                path: '/portal/login',
                query: {
                    redirect: to.fullPath
                }
            })
        } else {
            /*初始化一些信息*/
            next()
        }
    } else {
        next() // 确保一定要调用 next()
    }
})

export default router



